.footer {
padding:0 30px;

    &__list{
        display:flex;
        justify-content:space-between;
    }
    &__list a {
        width:40px;
        height:40px;
        z-index:15;
    }
    &__list img {
        width:100%;
        height:100%;
        object-fit:cover;
        pointer-events: none;
    }
}
